body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
}
.Main {
 
  border: 2px solid rgb(255, 255, 255);
  position: absolute;
  top: 40px;
  width: 303px;
  height: 303px;
  pointer-events: none;
  
}
.Winner {
  background: rgb(100, 229, 247);
  opacity: 0.7;
  border: 2px solid rgb(255, 255, 255);
  position: absolute;
  top: 40px;
  width: 303px;
  height: 303px;
  pointer-events: none;
  font-size: 72px;
  color: rgb(0, 0, 3);
}
.App {
  
  display: -webkit-flex;
  
  display: flex;
  
}
.Square {
  
  -webkit-transition: 0.3s;
  
  transition: 0.3s;
  width: 100px;
  height: 100px;
  border: 1px solid rgb(42, 170, 95);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 40px;
  color: rgb(108, 185, 236);
}
.Square:hover {
  
  background: rgb(245, 224, 224);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}
.Status {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.Container {
  text-align: center;
 
}
button {
  margin-top: 20px;
  background: rgb(3, 194, 3);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 100%;
  font-size: 14px;
}
button:hover {
  cursor: pointer;
  background: rgb(84, 248, 84);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-size: 20px;
}



